@font-face {
	font-family: 'Source Serif Pro';
	src: url('../fonts/SourceSerifPro_Bold/SourceSerifPro_Bold.eot');
	src: local('SourceSerifPro_Bold'), local('../fonts/SourceSerifPro_Bold/SourceSerifPro_Bold'),
		url('../fonts/SourceSerifPro_Bold/SourceSerifPro_Bold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/SourceSerifPro_Bold/SourceSerifPro_Bold.woff') format('woff'),
		url('../fonts/SourceSerifPro_Bold/SourceSerifPro_Bold.ttf') format('truetype');
	font-weight: 700;
	font-style: normal;
	font-display: swap;
}
